@tailwind base;
@tailwind components;
@tailwind utilities;

.app-container {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-color: #222;
  color: #777;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 2rem;
}

.input-container {
  width: calc(100vw - 3rem);
  max-width: 1200px;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
